.contato-container{
    height: 80vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background: #e2e2e2;



}

.contato{
    font-size: 2em;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    width: 100%;
    height: 10px;
    color: #da274b;

}

.icons-container{
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    text-align: center;
    align-items: center;

    
    
    
}

.icons{
    width: 60px;
    height: 60px;
    padding: 20px;
    border-radius: 100%;

}

.social-medias{
    font-size: 1.0em;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    width: 100%;
    height: 10px;
    
}

.social{
    padding: 40px;

}


.email{
    max-width: 800px;
    text-align: justify;
    color: rgb(54, 51, 51);
    font-style: italic;
    font-size: 20px;
}