.inicial-container{
    height: 100vh;
    flex-direction: row;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: rgb(21, 21, 21);
}

.aboutme{
    font-size: 2em;
    text-align: center;
    text-transform: uppercase;
    color: #da274b; 
    font-family: 'Sriracha', cursive;

}

.descricao{
    max-width: 800px;
    text-align: justify;
}

.boasvindas{
    font-style: italic;
    color: white;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    font-size: 4em;

}

.botao{
    padding: 2%;
    text-decoration: none;
    font-family: 'Raleway', sans-serif;
    color: white;
    text-transform: uppercase;



}

.setinha{
    width: 60px;
    height: 60px;
}

.setinha:hover{
    background: #d87085;
    border-radius: 50%;
    
}