.sobre-mim-container{
    height: 100vh;
    flex-direction: row;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #e2e2e2;

}

.foto-container{
    display: flex;
    float: left;
    
}

.foto-perfil{
    flex: 1;
    object-fit: contain;
    height: 300px;
    border-radius: 200px;

} 

.titulo{
    font-size: 2em;
    text-align: left;
    text-transform: uppercase;
    color: #da274b; 
    padding-bottom: 20px;
    font-family: 'Raleway', sans-serif;

    
}

.descricao{
    max-width: 800px;
    text-align: justify;
    color: rgb(54, 51, 51);
    font-family: 'Roboto', sans-serif;
    font-size: 20px;

    
}

.foto-socialmedia{
    width: 30%;
}

.apresentacao{
    width: 50%;
    text-align: center;
    float: right;
    

}

