.habilidades-container{
    height: 100vh;
    flex-direction: column;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.barrinha-container{
    border: 2px solid black;
    border-radius: 20px;
    flex: 1;
    display: flex;
    flex-direction: row;
    width: 100%;
}

.habilidade-container{
    display: flex;
    flex-direction: row;
    width: 80%;
}
.preenchimentoBarrinha{
    background-color:  #da274b;
    border-radius: 5px;
}

.lang{
    flex: 1;
    text-align: left;
    font-size: 1em;
    color: black;
}

.habilidades{
    font-size: 2em;
    text-align: left;
    text-transform: uppercase;
    color: #da274b;
    font-family: 'Raleway', sans-serif;

}