body{
    background-color:rgb(212, 199, 199);
    color: black;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
    
}

.navbar{
    position: fixed;
}
.navbar, .estoutriste{
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: row;
    height: 40px;
    
}


.navbar-item:hover, .estoutriste:hover{
    color: #f7f0f5;
    background: #da274b;

}
.estoutriste{
    color: #f7f0f5;
    height: 0px;

    
}

.space{
    flex:4;
}
.navbar-item, .estoutriste{
    flex: 1;
    text-align: center;
    font-size: 1em;
    text-decoration: none;
    background: black;
    font-family: 'Raleway', sans-serif;


    


}

